import React, { FC, SyntheticEvent, useState } from 'react'
import { useForm } from 'react-hook-form'
import * as yup from 'yup'
import { yupResolver } from '@hookform/resolvers/yup'
import FadeIn from 'react-fade-in';

import {FormWrapper,FormSuperWrapper,LinkButton,FormTitle,InputFormGroup,InputLabel,InputWrapper,Input,InputSymbolCount, SubmitButton,CheckboxWrapper,Checkbox,CheckboxInput,CheckboxLabel,CheckboxLink,InputError, CandyWrapper} from './styled'
import Axios from 'axios'

interface FormInputs {
    message: string
    name: string
    email: string
    city: string
    recipientEmail:string
    rules:boolean
  }

  const schema = yup.object().shape({
    message: yup.string().max(60,' i   i').required('     '),
    name: yup.string().max(14,'  14 i').required(`     `),
    city: yup.string().max(14,'  14 i').required('     '),
    recipientEmail:yup.string().trim().email('i  '),
    email: yup.string().trim().email('i  ').required('     '),
    rules: yup.bool().oneOf([true],`   '`).required('     ')
  })
  


export const Form:FC<{setIsForm: () => void}> = ({setIsForm}) => {
    const { register, handleSubmit, errors } = useForm<FormInputs>({ resolver: yupResolver(schema) })
    const [formSended,setFormSended] = useState(false)
    const [inputValues, setInputValues] = useState({
        message:'',name:'',city:'',rules:true
    })

    const onSubmit = async (data:FormInputs) =>{

        const res = await Axios.post('https://antonipetrenko.space/wishes/',{
            name:data.name,
            city:data.city,
            email:data.email,
            destinationTo: [data.recipientEmail],
            wish: data.message 
          
        })
        await res.status === 201 ? setFormSended(true) : setFormSended(false)
    }
    
    return(
        <FadeIn transitionDuration={2000}>
            <FormSuperWrapper>
                <CandyWrapper/>
        <FormWrapper onSubmit={handleSubmit(data => onSubmit(data))}>
        {!formSended ? <>       <FormTitle> </FormTitle>
            <InputFormGroup>
            <InputLabel> :</InputLabel>
            <InputWrapper>
                <Input onChange={(e: SyntheticEvent<HTMLInputElement, Event>) => setInputValues({...inputValues,message:e.currentTarget.value})} ref={register} name="message" type="text"/>
                <InputSymbolCount>{inputValues.message?.length}/60</InputSymbolCount>
                <InputError>
                    {errors.message?.message}
                </InputError>
                </InputWrapper>
            </InputFormGroup>
            <InputFormGroup>
            <InputLabel> :</InputLabel>
            <InputWrapper>
                <Input onChange={(e: SyntheticEvent<HTMLInputElement, Event>) => setInputValues({...inputValues,name:e.currentTarget.value})} ref={register} name="name" type="text"/>
                <InputSymbolCount>{inputValues.name?.length}/14</InputSymbolCount>
                <InputError>
                    {errors.name?.message}
                </InputError>
                </InputWrapper>
            </InputFormGroup>
            <InputFormGroup>
            <InputLabel>  :</InputLabel>
            <InputWrapper>
                <Input onChange={(e: SyntheticEvent<HTMLInputElement, Event>) => {setInputValues({...inputValues,city:e.currentTarget.value})}} ref={register} name="city" type="text"/>
    <InputSymbolCount>{inputValues.city?.length}/14</InputSymbolCount>
    <InputError>
                    {errors.city?.message}
                </InputError>
                </InputWrapper>
            </InputFormGroup>
            <InputFormGroup>
            <InputLabel> email:</InputLabel>
            <InputWrapper>
                <Input ref={register} name="email" type="email"/>
                <InputError>
                    {errors.email?.message}
                </InputError>
                </InputWrapper>
            </InputFormGroup>
            <InputFormGroup>
            <InputLabel>Email  *</InputLabel>
            <InputWrapper>
                <Input ref={register} name="recipientEmail" type="email"/>
                <InputError>
                    {errors.recipientEmail?.message}
                </InputError>
                </InputWrapper>
            </InputFormGroup>
             <CheckboxWrapper>
                 <Checkbox  checked={inputValues.rules}><CheckboxInput checked={inputValues.rules} onClick={(e: SyntheticEvent<HTMLInputElement, Event>) => setInputValues({...inputValues,rules:e.currentTarget.checked})}  name='rules' ref={register} type="checkbox"/></Checkbox>
                    <CheckboxLabel>   </CheckboxLabel>
                    <CheckboxLink> </CheckboxLink>
                    <InputError>
                    {errors.rules?.message}
                </InputError>
             </CheckboxWrapper>
            <SubmitButton> </SubmitButton></>
            : <>
             <FormTitle> i i!</FormTitle>
                <LinkButton onClick={() => {setFormSended(false);setIsForm()}}> i</LinkButton>
             </>
        }
      
        </FormWrapper>
        </FormSuperWrapper>
        </FadeIn>

    )
}